.bg-body {
	margin: 20px;
	display: grid;
	background-color: #fff;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	border-radius: 10px;
	-webkit-box-shadow: 1px 1px 1px #111;
	        box-shadow: 1px 1px 1px #111;
}

.display-data {
	display: grid;
	grid-gap: 10px;
	padding: 10px; 
	-ms-flex-pack: center; 
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;	
}



